body {
    background-color: rgb(250,250,250);
    background-image: url('./images/image2.jpg');
    background-repeat: no-repeat;
    background-size: cover;
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
        font-family: cursive;

}


.image{
    width: 370px;
    margin-top: 50px;

}

@media (max-width:770px){
    .container {
        margin: 0 10%
    }
    .image {
        width: 100%
    }
}