
.container{
margin: 50px 0;


}

.card {
    margin: 0 2% !important;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

   


}

.infected{
    border-bottom: 10px solid rgba(255, 0, 179, 0.5);
    



}
.recovered{
    border-bottom: 10px solid rgba(53, 119, 53, 0.986);
}
.deaths{
    border-bottom: 10px solid rgba(168, 26, 26, 0.897);
}

@media (max-width:770px){
    .card {
        margin: 2% 0 !important;
    }
   
}
